$colorbody: #010101;
$font-size-body: 18px;
$font-family-body:arial, sans-serif;
$margin-wrapper: 0 60px;

body {
    color: $colorbody;

    display: flex;
    font-size: $font-size-body;
    font-family: $font-family-body;

    flex-direction: column;
    margin: 0;
    min-height: 100vh;
  }
  .wrapper {
    margin: $margin-wrapper;
  }
  .header {
    background: lightgrey; /* Pasar a variable */
    height: 100px; /* Pasar a variable */
    padding: 10px 0;
    box-sizing: border-box;
  }
  .main {
    background: #aaa; /* Pasar a variable */
    flex: 1;
  }
  .main__title {
    color: #fff;
    font-size: 2.5em;
  }
  .main__content p {
    font-size: 1.1em;
    line-height: 140%;
  }
  .footer {
    background: lightgrey; /* Pasar a variable */
    height: 100px; /* Pasar a variable */
    padding: 10px 0;
    box-sizing: border-box;
  }
  
  